*{
    box-sizing: border-box;
}
html,body{
    height: 100%;
    width: 100%;
}

body {
    background: #03001C;
}

.stages{
    height: 50px;
    width: 80%;
    margin:auto;
    display: flex;
    direction: row;
    justify-content: space-evenly;
    align-items: center;
    background-color: #181344;
}

.autonomousbtn{
    width: 100%;
    height:100%;
    border: none;
}

.autonomousbtn:hover{
    cursor: pointer;
}

.teleopbtn{
    width: 100%;
    height:100%;
    border: none;
}

.teleopbtn:hover{
    cursor: pointer;
}

.score{
    width:30%;
    height:100%;
}

@media (max-width: 500px){
    .score{
        width:90%;
        display: flex;
        flex-direction: column-reverse;
    }

    .score h1{
        font-size: 2rem;
        text-align: center;
        height: 20px;
    }
    .score p{
        text-align: center;
    }
}

.total{
    text-align: left;
    color: #FFFFFF;
    font-size: 1.7rem;
    font-weight: 700;
    text-align:justify;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.partialscore{
    text-align:justify;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #FFFFFF;
    font-size: 1.2rem;
    font-weight: 300;
    height: 30px;
}
.partialscore p{
    display: inline;
}


.container{
    width:100%;
    height:100%;
}

.pallette{
    height: 50px;
    width: 80%;
    margin:auto;
    margin-top:50px;
    display: flex;
    direction: row;
    justify-content: space-evenly;
    align-items: center;
    background-color: #03001C;
}

.colorblock{
    cursor:pointer;
    display: inline-flex;
    height: 25px;
    margin: 0 7px 0 0;
    position: relative;
    width: 43px;
    border-radius: 10%;
    border-style: none;
}

.deletepixel{
    background-image:url("../images/erasericon.png");
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    cursor:pointer;
    display: inline-flex;
    height: 50px;
    width: 50px;
    margin: 5em;
    border-style: none;
    rotate: -45deg;
}


.pixelbuttons {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 40%;
    padding-top:73px;
}

@media (max-width:1080px){
    .pixelbuttons{
        width:90%;
    }
    .score{
        width:80%;
        margin-left: auto;
        margin-right: auto;
    }
}

.buttonsandscore{
    margin-top:30px;
    display: flex;
    align-items: start;
    justify-content: center; 
    flex-direction: row;
    width: 100%;
}

@media (max-width: 1080px) {
    .buttonsandscore{
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
    }
}


.pixelrow {
    height: 90px;
    margin-top: -37px;
}

.pixel {
    display: inline-flex;
    align-tracks: center;
    justify-content: center;
    height: 29px;
    margin: 0 7px 0 0;
    position: relative;
    width: 52px;
    border-radius: 10%;
    border-style: none;
}

.pixel p{
    margin: auto;
    font-size: 1rem;
}

.pixelBefore {
     content: "";
     position: absolute;
     top: -17px;
     left: 0;
     width: 0;
     height: 0;
     border-left: 26px solid transparent;
     border-right: 26px solid transparent;
     border-bottom: 19px solid;
}

.pixelAfter {
     content: "";
     position: absolute;
     top: 28px;
     left: 0;
     width: 0;
     height: 0;
     border-left: 26px solid transparent;
     border-right: 26px solid transparent;
     border-top: 19px solid;
}

@media(max-width: 500px){
    .pixel {
        display: inline-flex;
        height: 22px;
        margin: 0 7px 0 0;
        position: relative;
        width: 40px;
        border-radius: 10%;
        border-style: none;
    }
    
    .pixelBefore {
         content: "";
	     position: absolute;
         top: -16px;
         left: 0;
         width: 0;
         height: 0;
         border-left: 20px solid transparent;
         border-right: 20px solid transparent;
         border-bottom: 16px solid;
    }
    
    .pixelAfter {
         content: "";
	     position: absolute;
         top: 22px;
         left: 0;
         width: 0;
         height: 0;
         border-left: 20px solid transparent;
         border-right: 20px solid transparent;
         border-top: 16px solid;
    }
    .pixelrow {
        height: 82px;
        margin-top: -37px;
    }
}

@media(max-width: 365px){
    .pixel {
        display: inline-flex;
        height: 21px;
        margin: 0 6px 0 0;
        position: relative;
        width: 38px;
        border-radius: 10%;
        border-style: none;
    }
    
    .pixelBefore {
         content: "";
	     position: absolute;
         top: -16px;
         left: 0;
         width: 0;
         height: 0;
         border-left: 19px solid transparent;
         border-right: 19px solid transparent;
         border-bottom: 16px solid;
    }
    
    .pixelAfter {
         content: "";
	     position: absolute;
         top: 21px;
         left: 0;
         width: 0;
         height: 0;
         border-left: 19px solid transparent;
         border-right: 19px solid transparent;
         border-top: 16px solid;
    }
    .pixelrow {
        height: 80px;
        margin-top: -37px;
    }
}
.pixel:hover{
    cursor: pointer;
}
